.informacion-alumnos, .contain-certifaid {
  margin-top: 4rem;
}
.informacion-alumnos p, .contain-certifaid p {
  text-align: justify;
  font-size: 2.5rem;
  padding: 2rem;
}

.contenido-alumnos {
  display: flex;
  gap: 3rem;
  flex-direction: row;
  flex-flow: wrap;
  padding: 2rem;
}
.contenido-alumnos div {
  transition: 0.3s;
  width: 20%;
  margin: 0 auto;
}
.contenido-alumnos div h2 {
  text-align: center;
}
.contenido-alumnos div:hover {
  transform: scale(1.05);
  box-shadow: 0 0 40px -10px rgba(0, 0, 0, 0.25);
}