.informacion-alumnos, .contain-certifaid{
    margin-top: 4rem;
    p{
        text-align: justify;
        font-size: 2.5rem;
        padding: 2rem;
        
    }
}

.contenido-alumnos{
    display: flex;
    gap: 3rem;
    flex-direction: row;
    flex-flow: wrap;
    padding: 2rem;

    div{
        transition: 0.3s;
        width: 20%;
        margin: 0 auto;


        h2{
            text-align: center;
        }

        
    }

    

    div:hover{
        transform: scale(1.05);
        box-shadow: 0 0 40px -10px rgba(0,0,0, 0.25);
    }
}


